.example {
  color: #424242;
  max-width: 960px;
  background-color: #fff;
  padding: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #009688;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("demo.e3ba131c.jpg");
  background-position: center;
  background-size: cover;
}

.footer {
  background-color: var(--color-gray-background-footer);
  padding-block: 30px;
}

@media screen and (min-width: 768px) {
  .footer {
    justify-content: center;
    align-items: center;
    gap: 5px;
    display: flex;
  }
}

.footer__wrapper {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__wrapper {
    margin-top: 0;
  }
}

.footer__text {
  font-size: var(--font-14);
  text-align: center;
  margin-block: 0;
}

.footer__text--team {
  cursor: pointer;
  text-decoration: underline;
}

.footer__icon {
  fill: var(--color-orange);
}

:root {
  --font: "Roboto", sans-serif;
  --bg-color-header: #10181d;
  --bg-color-orange: #ff6b01;
  --color-white: #fff;
  --color-black: #000;
  --color-orange: #ff6b08;
  --color-red: #ff001b;
  --color-gray-footer: #545454;
  --color-gray-background-footer: #f7f7f7;
  --color-gray-modal: #8c8c8c;
  --font-12: 12px;
  --font-14: 14px;
  --font-30: 30px;
  --line-height-small: 1.17;
  --line-height-medium: 1.2;
  --line-height-large: 1.33;
  --line-height-extra-large: 1.67;
}

